    text-shadow: -10px 0 0 #5c3551, 10px 0 0 #5c3551;
}

.pets__back:hover {
    color: #5c3551;
}

.pets__heading {
    padding-top: 50px;
    text-align: center;
}

.pets__loader {
    position: relative;
    width: 50px;
    height: 4px;
    margin: 30px auto;
    border-radius: 5px;
    background-color: rgba(92, 53, 81, 0.5);
}

.pets__loader::after {
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    border-radius: 5px;
    background-color: #5c3551;
    top: 0;
    left: 0;
    -webkit-transform-origin: left center;
    -ms-transform-origin: left center;
    transform-origin: left center;
    -webkit-animation: loading 1.6s linear infinite;
    animation: loading 1.6s linear infinite;
}

@-webkit-keyframes loading {
    from {
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
    }

    to {
        -webkit-transform: scaleX(0);
        transform: scaleX(0);
    }
}

@keyframes loading {
    from {
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
    }

    to {
        -webkit-transform: scaleX(0);
        transform: scaleX(0);
    }
}

.pets__section {
    display: -ms-grid;
    display: grid;
    /* -ms-grid-columns: (100px)[4]; */
    grid-template-columns: repeat(4, 100px);
    -ms-grid-rows: 150px;
    grid-template-rows: 150px;
    grid-auto-rows: 150px;
    grid-row-gap: 10px;
    -webkit-box-pack: space-evenly;
    -ms-flex-pack: space-evenly;
    justify-content: space-evenly;
    padding: 25px 30px;
    max-width: 900px;
    margin: 0 auto;
}

.pets__section>*:nth-child(1) {
    -ms-grid-row: 1;
    -ms-grid-column: 1;
}

.pets__section>*:nth-child(2) {
    -ms-grid-row: 1;
    -ms-grid-column: 2;
}

.pets__section>*:nth-child(3) {
    -ms-grid-row: 1;
    -ms-grid-column: 3;
}

.pets__section>*:nth-child(4) {
    -ms-grid-row: 1;
    -ms-grid-column: 4;
}

@media(max-width: 600px) {
    .pets__section {
        /* -ms-grid-columns: (100px)[3]; */
        grid-template-columns: repeat(3, 100px);
    }
}

@media(max-width: 400px) {
    .pets__section {
        /* -ms-grid-columns: (100px)[2]; */
        grid-template-columns: repeat(2, 100px);
    }
}

.pet__item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    height: 100%;
    -webkit-transform: translateX(-20px);
    -ms-transform: translateX(-20px);
    transform: translateX(-20px);
    opacity: 0;
}

.pet__item.active {
    -webkit-animation: left .5s both;
    animation: left .5s both;
}

@-webkit-keyframes left {
    from {
        -webkit-transform: translateX(-20px);
        transform: translateX(-20px);
        opacity: 0;
    }

    to {
        -webkit-transform: translateX(0);
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes left {
    from {
        -webkit-transform: translateX(-20px);
        transform: translateX(-20px);
        opacity: 0;
    }

    to {
        -webkit-transform: translateX(0);
        transform: translateX(0);
        opacity: 1;
    }
}

.image__wrapper {
    border-radius: 90px;
    -ms-flex-preferred-size: 100px;
    flex-basis: 100px;
    overflow: hidden;
}

.pet__img {
    height: 100%;
    width: 100%;
    cursor: pointer;
}

.pet__img.active {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    width: 50%;
    height: auto;
    border-radius: 25px;
    -webkit-box-shadow: 0 0 5px 0 #5c3551;
    box-shadow: 0 0 5px 0 #5c3551;
}

.fas {
    margin-left: 20px;
}

.fa-arrow-alt-circle-left {
    margin-left: 0;
}

.vaccinated {
    color: rgba(255, 0, 0, 0.6);
}

.pet__btn {
    position: absolute;
    top: 10%;
    right: 10%;
}

.pet__date {
    font-size: 12px;
}